<template>
  <div id="app">
    <div class="container">
      <div class="Chart__list">
        <div class="Chart">
          <h2>Linechart</h2>
            <line-example></line-example>
          </div>
        </div>
    </div>
  </div>
</template>

<script>
import LineExample from './components/LineChart.js'
export default {
  name: 'App',
  components: {
    LineExample
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.container {
  max-width: 800px;
  margin:  0 auto;
}

 .Chart {
background: #bcc2ce;
border-radius: 15px;
box-shadow: 0px 2px 15px rgba(182, 42, 42, 0.27);
margin:  25px 0;
}
.Chart h2 {
margin-top: 0;
padding: 5px 0;
color:  rgba(255, 0,0, 0.5);
border-bottom: 1px solid #323d54;
}
</style>



